﻿.form {
  &__header {
    @extend %h2;
    margin-bottom: 40px;
    text-align: center;
    color: #fff;

    @media (min-width: $laptop-width) {
      text-align: left;
    }
  }

  &__input {
    @extend %input-font;
    @include hover(border-bottom-color, $hover-color);
    width: 100%;
    margin-bottom: 32px;
    border-bottom: 1px solid #fff;
    padding: 16px 0;
    background-color: $paige-color;

    &::placeholder {
      @extend %input-font;
      opacity: 0.7;
    }

    &:focus {
      outline: none;
      border-bottom-color: $hover-color;
    }
  }

  &__button {
    @extend %button;
  }
}
